<template>
    <div>
        <div class="autodraw-results">
            <template v-for="suggestion in suggestions">
                <figure class="autodraw-image" v-for="icon in suggestion.icons" :key="icon" @click="pickDrawSuggestion(icon, suggestion.name)">
                    <img :src="icon" width="50" height="50" :alt="suggestion.name" :title="suggestion.name">
                </figure>
            </template>
        </div>
        <div>{{translated}}</div>
        <div>{{src}}</div>
        <div class="canvas-container" style="user-select: none;">
            <canvas ref="c" id="c" width="800px" height="800px" style="border: 1px solid rgb(170, 170, 170);  touch-action: none; user-select: none;" class="lower-canvas"></canvas>
            <!-- <canvas class="upper-canvas " style="border: 1px solid rgb(170, 170, 170); touch-action: none; user-select: none; cursor: crosshair;"></canvas> -->

            <div class="canvas-controller" style="display: inline-block; margin-left: 10px; margin-top: 100px;">
                <button id="drawing-mode" class="btn btn-info">Cancel drawing mode</button><br>
                <button id="clear-canvas" @click="clearCanvas" class="btn btn-info">Clear</button><br>

                <div id="drawing-mode-options">
                    <label for="drawing-mode-selector">Mode:</label>
                    <select id="drawing-mode-selector">
                    <option>Pencil</option>
                    <option>Circle</option>
                    <option>Spray</option>
                    <option>Pattern</option>

                    <option>hline</option>
                    <option>vline</option>
                    <option>square</option>
                    <option>diamond</option>
                    <option>texture</option>
                    </select><br>

                    <label for="drawing-line-width">Line width:</label>
                    <span class="info">10</span><input type="range" value="30" min="0" max="150" id="drawing-line-width"><br>

                    <label for="drawing-color">Line color:</label>
                    <input type="color" value="#005E7A" id="drawing-color"><br>

                    <label for="drawing-shadow-color">Shadow color:</label>
                    <input type="color" value="#005E7A" id="drawing-shadow-color"><br>

                    <label for="drawing-shadow-width">Shadow width:</label>
                    <span class="info">0</span><input type="range" value="0" min="0" max="50" id="drawing-shadow-width"><br>

                    <label for="drawing-shadow-offset">Shadow offset:</label>
                    <span class="info">0</span><input type="range" value="0" min="0" max="50" id="drawing-shadow-offset"><br>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import Axios from 'axios';
import {fabric} from 'fabric';
export default {
    data(){
        return{
            API_ENDPOINT : 'https://inputtools.google.com/request?ime=handwriting&app=autodraw&dbg=1&cs=1&oe=UTF-8',
            stencils: [],
            suggestions:[],
            canvas: null,
            preX: 0,
            preY: 0,
            curX:  0,
            curY : 0,
            pressedAt: 0,
            isPressing: false,
            maxX: 100000,
            minX: 0,
            maxY: 100000,
            minY: 0,
            shapes: [],
            curShape:[],
            srcText:{},
            translated: '',
            src:''
        }
    },
    methods:{
        loadStencils(){
            this.stencils = require('@/assets/stencils.json')
            
        },
        onDrawingMouseDown(e){
            this.isPressing = true;

            if(e.pointer.x < 0){
                e.pointer.x = 0;
            }
            if(e.pointer.y < 0){
                e.pointer.y = 0;
            }
            
            this.clearCurShape();
            this.curShape[0].push(e.pointer.x)
            this.curShape[1].push(e.pointer.y)
            this.curShape[2].push(Date.now() - this.pressedAt);
            console.dir(e)

            // console.log('down')
        },
        onDrawingMouseMove(e){
            if(!this.isPressing) return;
            if(e.pointer.x < 0){
                e.pointer.x = 0;
            }
            if(e.pointer.y < 0){
                e.pointer.y = 0;
            }
            
            this.curShape[0].push(e.pointer.x)
            this.curShape[1].push(e.pointer.y)
            this.curShape[2].push(Date.now() - this.pressedAt);
            // console.log('move')

            (e.pointer.x > this.maxX) && (this.maxX = e.pointer.x);
            (e.pointer.x < this.minX) && (this.minX = e.pointer.x);
            (e.pointer.y > this.maxY) && (this.maxY = e.pointer.y);
            (e.pointer.y < this.minY) && (this.minY = e.pointer.y);
        },
        onDrawingMouseUp(){
            this.isPressing = false;
            this.shapes.push(this.curShape);
            
            this.getDrawSuggetions()
            this.updateObjectBox()
        },
        getDrawSuggetions(){
            Axios.post(this.API_ENDPOINT, {
                input_type : 0,
                requests: [{
                    ink: this.shapes,
                    language : 'autodraw',
                    writing_guide: {
                        "width": this.canvas.width,
                        "height":this.canvas.height
                    }
                }]
            })
            .then(({data}) =>{
                console.dir(data)
                let results = JSON.parse(data[1][0][3].debug_info.match(/SCORESINKS: (.*) Service_Recognize:/)[1])
                    .map(result => {
                        return {
                            name: result[0],
                            icons: (this.stencils[result[0]] || []).map(collection => collection.src)
                        }
                    });
                console.dir(results)
                this.suggestions = results;
                
            })
        },
        pickDrawSuggestion(icon, name){
            this.clearCanvas();
            console.log('======================')
            console.log(name)
            this.src = name;

            if(this.srcText[name]){
                this.translated = this.srcText[name]
            }else{
                Axios.get(`/translate?text=${name}`)
                .then(({data}) => {
                    this.srcText[name] = this.translated = data;
                    console.log(this.translated)
                    console.dir(this.srcText)
                })
                .catch(() => {
                    alert('error')
                })
            }

            let image = new Image();
            
            let imageWidth = 400;
            image.src = icon;
            let imageHeight = 400;
            image.onload = () => this.canvas.getContext('2d').drawImage(image, 
                        this.canvas.width / 2 - imageWidth / 2, this.canvas.height / 2 - imageHeight / 2, imageWidth, imageHeight);
            
        },
        clearCanvas(){
            this.canvas.clear();
            this.shapes = [];
            this.clearCurShape();
        },
        updateObjectBox(){
           
        },
        clearObjectBox(){

        },
        clearCurShape(){
           this.curShape = [
               [],
               [],
               []
           ]
        },
        setEventHandlers(){
            this.pressedAt = Date.now();
            this.canvas.on('mouse:down',this.onDrawingMouseDown)
            this.canvas.on('mouse:move',this.onDrawingMouseMove)
            this.canvas.on('mouse:up',this.onDrawingMouseUp)
        }
    },
    mounted(){
        this.loadStencils();
        
        if(document.readyState == 'complete'){
            prepareCanvas();
            this.canvas = window.__canvas;
            this.setEventHandlers();
        }else{
            window.onload = () => {
                prepareCanvas();
                this.canvas = window.__canvas
                this.setEventHandlers();
            } 
        }

        function prepareCanvas() {
            var $ = function(id){return document.getElementById(id)};
            
            var canvas = window.__canvas = new fabric.Canvas('c', {
                isDrawingMode: true
            });
            


            fabric.Object.prototype.transparentCorners = false;

            var drawingModeEl = $('drawing-mode'),
                drawingOptionsEl = $('drawing-mode-options'),
                drawingColorEl = $('drawing-color'),
                drawingShadowColorEl = $('drawing-shadow-color'),
                drawingLineWidthEl = $('drawing-line-width'),
                drawingShadowWidth = $('drawing-shadow-width'),
                drawingShadowOffset = $('drawing-shadow-offset');


            drawingModeEl.onclick = function() {
                canvas.isDrawingMode = !canvas.isDrawingMode;
                if (canvas.isDrawingMode) {
                    drawingModeEl.innerHTML = 'Cancel drawing mode';
                    drawingOptionsEl.style.display = '';
                }
                else {
                    drawingModeEl.innerHTML = 'Enter drawing mode';
                    drawingOptionsEl.style.display = 'none';
                }
            };

            if (fabric.PatternBrush) {
                var vLinePatternBrush = new fabric.PatternBrush(canvas);
                vLinePatternBrush.getPatternSrc = function() {

                    var patternCanvas = fabric.document.createElement('canvas');
                    patternCanvas.width = patternCanvas.height = 10;
                    var ctx = patternCanvas.getContext('2d');

                    ctx.strokeStyle = this.color;
                    ctx.lineWidth = 5;
                    ctx.beginPath();
                    ctx.moveTo(0, 5);
                    ctx.lineTo(10, 5);
                    ctx.closePath();
                    ctx.stroke();

                    return patternCanvas;
                };

                var hLinePatternBrush = new fabric.PatternBrush(canvas);
                hLinePatternBrush.getPatternSrc = function() {

                    var patternCanvas = fabric.document.createElement('canvas');
                    patternCanvas.width = patternCanvas.height = 10;
                    var ctx = patternCanvas.getContext('2d');

                    ctx.strokeStyle = this.color;
                    ctx.lineWidth = 5;
                    ctx.beginPath();
                    ctx.moveTo(5, 0);
                    ctx.lineTo(5, 10);
                    ctx.closePath();
                    ctx.stroke();

                    return patternCanvas;
                };

                var squarePatternBrush = new fabric.PatternBrush(canvas);
                squarePatternBrush.getPatternSrc = function() {

                    var squareWidth = 10, squareDistance = 2;

                    var patternCanvas = fabric.document.createElement('canvas');
                    patternCanvas.width = patternCanvas.height = squareWidth + squareDistance;
                    var ctx = patternCanvas.getContext('2d');

                    ctx.fillStyle = this.color;
                    ctx.fillRect(0, 0, squareWidth, squareWidth);

                    return patternCanvas;
                };

                var diamondPatternBrush = new fabric.PatternBrush(canvas);
                diamondPatternBrush.getPatternSrc = function() {

                    var squareWidth = 10, squareDistance = 5;
                    var patternCanvas = fabric.document.createElement('canvas');
                    var rect = new fabric.Rect({
                    width: squareWidth,
                    height: squareWidth,
                    angle: 45,
                    fill: this.color
                    });

                    var canvasWidth = rect.getBoundingRect().width;

                    patternCanvas.width = patternCanvas.height = canvasWidth + squareDistance;
                    rect.set({ left: canvasWidth / 2, top: canvasWidth / 2 });

                    var ctx = patternCanvas.getContext('2d');
                    rect.render(ctx);

                    return patternCanvas;
                };

                var img = new Image();
                img.src = '../assets/images/honey_im_subtle.png';

                var texturePatternBrush = new fabric.PatternBrush(canvas);
                texturePatternBrush.source = img;
            }

            $('drawing-mode-selector').onchange = function() {

                if (this.value === 'hline') {
                    canvas.freeDrawingBrush = vLinePatternBrush;
                }
                else if (this.value === 'vline') {
                    canvas.freeDrawingBrush = hLinePatternBrush;
                }
                else if (this.value === 'square') {
                    canvas.freeDrawingBrush = squarePatternBrush;
                }
                else if (this.value === 'diamond') {
                    canvas.freeDrawingBrush = diamondPatternBrush;
                }
                else if (this.value === 'texture') {
                    canvas.freeDrawingBrush = texturePatternBrush;
                }
                else {
                    canvas.freeDrawingBrush = new fabric[this.value + 'Brush'](canvas);
                }

                if (canvas.freeDrawingBrush) {
                    var brush = canvas.freeDrawingBrush;
                    brush.color = drawingColorEl.value;
                
                    if (brush.getPatternSrc) {
                    brush.source = brush.getPatternSrc.call(brush);
                    }
                    brush.width = parseInt(drawingLineWidthEl.value, 10) || 1;
                    brush.shadow = new fabric.Shadow({
                    blur: parseInt(drawingShadowWidth.value, 10) || 0,
                    offsetX: 0,
                    offsetY: 0,
                    affectStroke: true,
                    color: drawingShadowColorEl.value,
                    });
                }
            };

            drawingColorEl.onchange = function() {
                var brush = canvas.freeDrawingBrush;
                brush.color = this.value;
                if (brush.getPatternSrc) {
                    brush.source = brush.getPatternSrc.call(brush);
                }
            };
            drawingShadowColorEl.onchange = function() {
                canvas.freeDrawingBrush.shadow.color = this.value;
            };
            drawingLineWidthEl.onchange = function() {
                canvas.freeDrawingBrush.width = parseInt(this.value, 10) || 1;
                this.previousSibling.innerHTML = this.value;
            };
            drawingShadowWidth.onchange = function() {
                canvas.freeDrawingBrush.shadow.blur = parseInt(this.value, 10) || 0;
                this.previousSibling.innerHTML = this.value;
            };
            drawingShadowOffset.onchange = function() {
                canvas.freeDrawingBrush.shadow.offsetX = parseInt(this.value, 10) || 0;
                canvas.freeDrawingBrush.shadow.offsetY = parseInt(this.value, 10) || 0;
                this.previousSibling.innerHTML = this.value;
            };

            if (canvas.freeDrawingBrush) {
                // console.dir(canvas.freeDrawingBrush)
                // console.dir(canvas)
                canvas.freeDrawingBrush.color = drawingColorEl.value;
                if (canvas.freeDrawingBrush.getPatternSrc) {
                    canvas.freeDrawingBrush.source = canvas.freeDrawingBrush.getPatternSrc.call(this);
                }
            
                canvas.freeDrawingBrush.width = parseInt(drawingLineWidthEl.value, 10) || 1;
                canvas.freeDrawingBrush.shadow = new fabric.Shadow({
                    blur: parseInt(drawingShadowWidth.value, 10) || 0,
                    offsetX: 0,
                    offsetY: 0,
                    affectStroke: true,
                    color: drawingShadowColorEl.value,
                });
            }

            /////////////////////
            var canvases = window.__canvases || window.canvases;

            canvas && canvas.calcOffset && canvas.calcOffset();

            if (canvases && canvases.length) {
                for (var i = 0, len = canvases.length; i < len; i++) {
                    canvases[i].calcOffset();
                }
            }
        }        
  }
}
</script>

<style scoped>
.canvas-container{
    margin: auto;
    padding: auto;
    width: 90%;
    height: 800px;
    display: flex;
}
.autodraw-results{
    display: flex;
}
#drawing-mode {
  margin-bottom: 10px;
  vertical-align: top;
}
#drawing-mode-options {
  display: inline-block;
  vertical-align: top;
  margin-bottom: 10px;
  margin-top: 10px;
  background: #f5f2f0;
  padding: 10px;
}
label {
  display: inline-block; width: 130px;
}
.info {
  display: inline-block;
  width: 25px;
  background: #ffc;
}
#bd-wrapper {
  min-width: 1500px;
}
</style>