<template>
  <div>
    <div class="canvas-container" style="width: 500px; height: 100; position: relative; top: 10px; user-select: none;">
      <canvas :ref="canvasTmp" :id="canvasTmp" width="625" height="100" style="border: 1px solid rgb(170, 170, 170); position: absolute; left: 0px; top: 0px; touch-action: none; user-select: none;" class="lower-canvas"></canvas>
      <!-- <canvas class="upper-canvas " width="625" height="625" style="border: 1px solid rgb(170, 170, 170); position: absolute; width: 500px; height: 500px; left: 0px; top: 0px; touch-action: none; user-select: none; cursor: crosshair;"></canvas> -->
    </div>
    <div style="display: inline-block; margin-top: 10px;"></div>
  </div>
</template>
<script>
import {fabric} from 'fabric';

export default {
  name: 'DictationCanvas',
  props: {
    canvasId: {
      type: String,
      description: "canvas Id",
      default:"canvas1"
    },
  },
  data() {
    return {
      canvasTmp:this.canvasId
    };
  },
  mounted(){
    var canvas = window.__canvas = new fabric.Canvas(this.canvasTmp, {
      isDrawingMode: true
    });
    if( window.__canvasList==null){
      window.__canvasList=[];
    }
    window.__canvasList.push(canvas);
    // console.dir(canvas);
    fabric.Object.prototype.transparentCorners = false;
    
    if (canvas.freeDrawingBrush) {
      canvas.freeDrawingBrush.color = "#000000";
      canvas.freeDrawingBrush.width = 1;
      canvas.freeDrawingBrush.shadow = new fabric.Shadow({
        blur: 0,
        offsetX: 0,
        offsetY: 0,
        affectStroke: true,
        color: "#000000",
      });
    }
  }
}
</script>
