<template>
  <div>
    <div class="canvas-container" style="user-select: none; position:relative;">
      <canvas ref="c" id="c" style="position:relative; border: 1px solid rgb(170, 170, 170);  touch-action: none; user-select: none;" class="lower-canvas"></canvas>
    </div>
  </div>
</template>

<script>
// import Axios from 'axios';
import {fabric} from 'fabric';
import Axios from 'axios';

export default {
    data(){
        return{
        canvas: null,
        scaleX: null,
        scaleY: null,
        isPressing: false,
        path:[],
        pointSize: 50,
        canvasWidth : 800,
        canvasHeight : 800,
        curPathIdx : 0,
        lastPointIdx : -1,
        isComplete : false
        }
    },
    methods:{
        validate(pathIdx, pointIdx){
            console.log(pathIdx)
            
            if(pathIdx != this.curPathIdx || pointIdx != this.lastPointIdx + 1){
                console.log('실패')
                return false
            }
                
            if(pointIdx == this.path[this.curPathIdx].length - 1){//해당 획의 핀포인트들을 다 방문한 경우
                this.isComplete = true
            }
            
            this.lastPointIdx = pointIdx

            return true
        },
        mouseUp(){
            this.isPressing = false
            
            if(this.isComplete){
                
                this.curPathIdx++

                if(this.curPathIdx == this.path.length){
                    alert('성공')
                }else{
                    alert('next path')
                    this.setPath()
                }
            }else{
                let timerId = null
                timerId = setInterval(()=>{
                    if(this.canvas._objects.length > 1){
                        this.undo()
                        clearInterval(timerId)
                        this.showAnimation()
                    }
                },100)
            }
        },
        mouseDown(){
            this.isPressing = true
        },

        setEventHandlers(){
            let canvasContainer = document.getElementsByClassName('canvas-container')[0]
            ///////////////////////////////////////////////Bubling을 활용해 canvas와 div태그로부터 전파되는 이벤트 감지
            canvasContainer.addEventListener('mousedown', this.mouseDown)
            canvasContainer.addEventListener('mouseup', this.mouseUp)
        
            ////////////////////////////////////////////
            // this.canvas.on('mouse:move',this.onDrawingMouseMove)
        },
        setPath(){
            this.lastPointIdx = -1
            this.isComplete = false

            let canvasContainer = document.getElementsByClassName('canvas-container')[0]
            let upperCanvas = document.getElementsByClassName('upper-canvas')[0]
            let color = ['red', 'blue', 'green', 'black']
            

            let pathIdx = this.curPathIdx;

            this.path[this.curPathIdx].forEach( (point, i) => {
                
                let div = document.createElement('div')
                div.classList.add('point')
                
                div.style.position = 'absolute';
                div.style.width = this.pointSize + 'px'
                div.style.height = this.pointSize + 'px'
                div.style.cursor = 'crosshair'

                //////////////////////////////////////
                div.style.backgroundColor = color[i % 4]
                
                ///////////////////////////////////////


                div.style.left =  point[0] * this.scaleX - (this.pointSize / 2) + 'px'
                div.style.top =  point[1] * this.scaleY - (this.pointSize / 2) + 'px'

                div.addEventListener('mouseover', (e) => {
                    if(this.isPressing){
                        this.validate(pathIdx, i)
                    }

                    let nEvent = new MouseEvent(e.type, e)
                    upperCanvas.dispatchEvent(nEvent)
                })
                div.addEventListener('mousemove', (e) => {
                    let nEvent = new MouseEvent(e.type, e)
                    upperCanvas.dispatchEvent(nEvent)
                })
                div.addEventListener('mousedown', (e) => {
                    if(i == 0){
                        this.validate(pathIdx, i)
                    }

                    let nEvent = new MouseEvent(e.type, e)
                    upperCanvas.dispatchEvent(nEvent)
                })

                canvasContainer.appendChild(div)
            } )

            this.showAnimation()
        },
        initAnimation(){
            
            var lastTime = 0;
            var vendors = ['ms', 'moz', 'webkit', 'o'];
            for (var x = 0; x < vendors.length && !window.requestAnimationFrame; ++x) {
                window.requestAnimationFrame = window[vendors[x] + 'RequestAnimationFrame'];
                window.cancelAnimationFrame = window[vendors[x] + 'CancelAnimationFrame'] || window[vendors[x] + 'CancelRequestAnimationFrame'];
            }

            if (!window.requestAnimationFrame) window.requestAnimationFrame = function (callback) {
                var currTime = new Date().getTime();
                var timeToCall = Math.max(0, 16 - (currTime - lastTime));
                var id = window.setTimeout(function () {
                    callback(currTime + timeToCall);
                },
                timeToCall);
                lastTime = currTime + timeToCall;
                return id;
            };

            if (!window.cancelAnimationFrame) window.cancelAnimationFrame = function (id) {
                clearTimeout(id);
            };
            
        },
        undo(){
            this.canvas._objects.pop()
            this.canvas.renderAll()
        },
        showAnimation(){
            var ctx = this.canvas.getContext("2d");
            ctx.lineCap = "round";

            // variable to hold how many frames have elapsed in the animation
            var t = 1;

            // define the path to plot
            var vertices = [];

            this.path[this.curPathIdx].forEach( (point) => {
                vertices.push({
                    x : point[0] * this.scaleX,
                    y : point[1] * this.scaleY
                })
            })


            // set some style
            ctx.lineWidth = 5;
            ctx.strokeStyle = "blue";
            // calculate incremental points along the path
            var frame = 10
            var points = calcWaypoints(vertices);
            // extend the line from start to finish with animation
            animate(points);


            // calc waypoints traveling along vertices
            function calcWaypoints(vertices) {
                var waypoints = [];

                for (var i = 1; i < vertices.length; i++) {
                    var pt0 = vertices[i - 1];
                    var pt1 = vertices[i];
                    var dx = pt1.x - pt0.x;
                    var dy = pt1.y - pt0.y;
                    for (var j = 0; j < frame; j++) {
                        var x = pt0.x + dx * j / frame;
                        var y = pt0.y + dy * j / frame;
                        waypoints.push({
                            x: x,
                            y: y
                        });
                    }
                }
                return (waypoints);
            }


            function animate() {
                if (t < points.length - 1) {
                    requestAnimationFrame(animate);
                }
                // draw a line segment from the last waypoint
                // to the current waypoint
                ctx.beginPath();
                ctx.moveTo(points[t - 1].x, points[t - 1].y);
                ctx.lineTo(points[t].x, points[t].y);
                ctx.stroke();
                // increment "t" to get the next waypoint
                t++;
            }
        },
        setImage(){
            let letter = this.$route.query.letter
            let uri = encodeURI(`letter/${letter}`)
            console.log(uri)
            
            Axios.get(`/${uri}.json`)
            .then(({data})=>{
                
                this.scaleX = this.canvasWidth / data.imgWidth
                this.scaleY = this.canvasHeight / data.imgHeight
                
                fabric.Image.fromURL(`${this.$store.getters.BACKEND_URL}/${uri}.png`, (img) =>{
                    img.set({
                        left:0,
                        top: 0,
                        scaleX: this.scaleX,
                        scaleY: this.scaleY
                    })
                    console.dir(img)
                    this.canvas.add(img)
                })

                
                this.path = data.path
                this.curPathIdx = 0;
                this.setPath()
            })
            .catch(() => {
                alert('error')
            })
        },
        init(){
            this.prepareCanvas();
            this.canvas = window.__canvas;
            let container = document.getElementsByClassName('canvas-container')[0]
            container.style.width = this.canvasWidth + 'px'
            container.style.height = this.canvasHeight + 'px'
            this.setEventHandlers();
            this.initAnimation();
            this.setImage()
        },
        prepareCanvas() {
            let canvasHeight = this.canvasHeight
            let canvasWidth = this.canvasWidth
        
            var canvas = window.__canvas = new fabric.Canvas('c', {
                isDrawingMode: true,
                width: canvasWidth,
                height: canvasHeight
            });

            canvas.freeDrawingBrush.width = 10;
            canvas.freeDrawingBrush.color = 'green'

            fabric.Object.prototype.transparentCorners = false;

            /////////////////////
            var canvases = window.__canvases || window.canvases;

            canvas && canvas.calcOffset && canvas.calcOffset();

            if (canvases && canvases.length) {
                for (var i = 0, len = canvases.length; i < len; i++) {
                    canvases[i].calcOffset();
                }
            }
        },    
  },
  mounted(){
    if(document.readyState == 'complete'){
      this.init()
    }else{
      window.onload = this.init
    }
    // document.getElementsByClassName('upper-canvas')[0].addEventListener('click', ()=> {console.log('upper')})
  }
}
</script>

<style>
.canvas-container{
  margin: auto;
  padding: auto;
  display: flex;
}
.point{
  opacity: 0;
}

label {
display: inline-block; width: 130px;
}
.info {
  display: inline-block;
  width: 25px;
  background: #ffc;
}
#bd-wrapper {
  min-width: 1500px;
}
</style>