import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    BACKEND_URL : 'http://k3d206.p.ssafy.io:9999',
    userInfo : null,
    class : null
  },
  getters: {
    BACKEND_URL(state){
      return state.BACKEND_URL
    },
    userInfo(state) {
      return state.userInfo
    },
    classInfo(state){
      return state.class
    }
  },
  mutations: {
    LOGIN(state, payload) {
      state.userInfo = payload;
    },
    LOGOUT(state) {
      state.userInfo = null;
    },
    CLASS(state, payload) {
      state.class = payload;
    },
  },
  actions: {
    LOGIN(context, data) {
      context.commit("LOGIN", data);
    },
    LOGOUT(context) {
      context.commit("LOGOUT");
    },
    CLASS(context, data) {
      context.commit("CLASS", data);
    },
  },
  modules: {
  }
})
